import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';
import { MeetSomebodyPage } from '../pages/meet-somebody/meet-somebody';
import { ContactsListPage } from '../pages/contacts-list/contacts-list';
import { SettingsPage } from '../pages/settings/settings';
import { LoginPage } from '../pages/login/login';
import { UserProfilePage } from '../pages/user-profile/user-profile';
import { MyProfilePage } from '../pages/my-profile/my-profile';
import { ChatroomPage } from '../pages/chatroom/chatroom';
import {WaitlistPage} from '../pages/waitlist/waitlist';
import {VIPPage} from '../pages/vip/vip';
import {PurchasePage} from '../pages/purchase/purchase';
import { GalleryComponent } from '../components/gallery/gallery';
import { ChatBubbleComponent } from '../components/chat-bubble/chat-bubble';
import { ProfilePostsComponent } from '../components/profile-posts/profile-posts';
import { ProfileTimelineComponent } from '../components/profile-timeline/profile-timeline';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

@NgModule({
  declarations: [
    MyApp,
    MeetSomebodyPage,
    ContactsListPage,
    SettingsPage,
    LoginPage,
    UserProfilePage,
    MyProfilePage,
    ChatroomPage,
    WaitlistPage,
    VIPPage,
    PurchasePage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/filter/filter.module#FilterPageModule', name: 'FilterPage', segment: 'filter', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/purchase/purchase.module#PurchasePageModule', name: 'PurchasePage', segment: 'purchase', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/vip/vip.module#VipPageModule', name: 'VIPPage', segment: 'vip', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/waitlist/waitlist.module#WaitlistPageModule', name: 'WaitlistPage', segment: 'waitlist', priority: 'low', defaultHistory: [] }
  ]
})
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    MeetSomebodyPage,
    ContactsListPage,
    SettingsPage,
    LoginPage,
    UserProfilePage,
    MyProfilePage,
    ChatroomPage,
    WaitlistPage,
    VIPPage,
    PurchasePage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



